.courses-page {
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 22%,
    rgba(87, 28, 96, 1) 57%,
    rgba(96, 0, 255, 0.8141631652661064) 100%
  );
  height: 100vh;
  background-color: inherit;
}

.courses-list {
  width: 300px;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.courses-list div {
  color: #fff;
}
